<template>
  <a-card title="每日壁纸" class="card2" :loading="loadingstatus" :bordered="false">
    <div slot="extra" href="#" class="more"></div>
    <a :href="dayimg.img_1920" target="_blank">
      <a-card hoverable style="width: 100%">
        <img
          slot="cover"
          alt="每日壁纸"
          :src="dayimg.img_1920"
        />
        <a-card-meta :title="dayimg.copyright">
          <template slot="description" v-if="dayimg.date">
            更新时间：{{dayimg.date.slice(0,4)}}-{{dayimg.date.slice(4,6)}}-{{dayimg.date.slice(6,8)}}
          </template>
        </a-card-meta>
      </a-card>
    </a>
  </a-card>
</template>

<script>

export default {
  data(){
    return{
      //壁纸信息
      dayimg:[],
      //接口地址
      Api:{
        getimage:'https://route.showapi.com/1287-1?showapi_appid=674964&showapi_sign=40d10f5336274f45bb4eb7a9b11c1d1e',
      },
      loadingstatus:true,
    }
  },
  methods:{
    //图片接口
    getimage(){
      this.loadingstatus=true
      return this.$axios.post(this.Api.getimage,{}).then(res=>{
        // console.log(res.data,'getimage');
        this.dayimg=res.data.showapi_res_body.data
        this.loadingstatus=false
      }).catch(err=>{
        console.log(err);
      })
    },
  },
  mounted(){
    this.getimage()
  },
}
</script>

<style scoped>
  .card2{
    width: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #cac8c8;
    margin-top: 20px;
  }
  .more{
    background-color: #10AEB5;
    box-shadow: 0 0 5px #10AEB5;
    width: 10px;
    height: 30px;
    border-radius: 5px;
  }
</style>


