import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import gravity from '@/components/gravity'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/zlq',
      name: 'gravity',
      component: gravity,
      meta:{
        title:'重力球'
      }
    }
  ]
})



