<template>
  <a-card title="" class="card2" :bordered="false">
    <a-spin :spinning="spinning">
      <a-row style="font-size:1.2em;font-weight:700">
        <a-col :span=24>{{famousdata.content}}</a-col>
        <a-col style="float:right" v-if="famousdata.author">—— {{famousdata.author}}</a-col>
      </a-row>
    </a-spin>
  </a-card>
</template>

<script>
export default {
  data(){
    return{
      spinning:true,
      Api:{
        getlist:'https://v2.alapi.cn/api/mingyan'
      },
      famousdata:{
        author:'',
        content:'',
      },
    }
  },
  methods:{
    //名人名言接口
    getlist(){
      return this.$axios.get(this.Api.getlist,{
        params:{
          token:'LwExDtUWhF3rH5ib',
          typeid:Math.floor(Math.random()*45+1).toString(),
        }
      }).then(res=>{
        if(res.data.data){
          this.famousdata=res.data.data
          this.spinning=false
          console.log(this.famousdata,'名人名言');
        }        
      }).catch(err=>{
        console.log(err);
      })
    }
  },
  mounted(){
    this.getlist()
  }
}
</script>

<style scoped>
  .card2{
    width: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #cac8c8;
    margin-top: 20px;
  }
  .more{
    background-color: #10AEB5;
    box-shadow: 0 0 5px #10AEB5;
    width: 10px;
    height: 30px;
    border-radius: 5px;
  }
</style>


