<template>
  <div>
    <a-card class="card2" :bordered="false" :loading="loadingstatus">
      <div slot="title" href="#" class="title">*公安部全网通缉令</div>
      <div slot="extra" href="#" class="more"></div>      
      <a-row :gutter=[10,10]>
        <a-col :xs="{ span: 12}" :lg="{ span: 6}" v-for="(data,index) in wantedcircular.data" :key="index">
            <a-card hoverable class="acardhover" @click="clickdata(data)">
            <div slot="title" href="#" class="name">{{data.name}}</div>
            <div slot="extra" href="#"><span style="color:#1890FF">详情</span></div>
              <!-- <a-card-meta :title="data.name" class="carddata"> -->
                <div class="carddata">
                  <span>身份证号：</span><span>{{data.id_card}}</span><br/>
                  <span>户籍：</span><span>{{data.hu_ji}}</span><br/>
                  <!-- <span>性别：</span><span>{{data.sex}}</span><br/> -->
                  <!-- <span>发布者：</span><span>{{data.child_feature}}</span><br/> -->
                  <span class="spandata">案发经过：</span><span>{{data.lost_place}}</span><br/>
                </div>
              <!-- </a-card-meta> -->
            </a-card>
        </a-col>
      </a-row>
      <!-- {{wantedcircular}} -->
    </a-card>
    <a-modal v-model="visible">
      <div slot="title">
        <span class="name" style="font-size:1.2em">{{modalwantedcircular.name}}</span>
      </div>
      <div slot="footer">
        <a-button type="danger" @click="clickOk">确认</a-button>
      </div>
      <div style="margin-bottom:20px">
        <p class="p">性别：{{modalwantedcircular.sex}}</p>
        <p class="p">出生日期：{{modalwantedcircular.birth_time}}</p>
        <p class="p">身份证号：{{modalwantedcircular.id_card}}</p>
        <p class="p">户籍：{{modalwantedcircular.hu_ji}}</p>
        <p class="p">案发经过：{{modalwantedcircular.lost_place}}</p>
        <br/>
        <span class="p" style="float:right">{{modalwantedcircular.child_feature}}</span>
      </div>
      
    </a-modal>
  </div>
</template>

<script>
export default {
  data(){
    return{
      loadingstatus:true,
      //通缉信息
      wantedcircular:{
        contacts:[],
        data:[],
      },
      //接口地址
      Api:{
        wanted:'https://api.isoyu.com/gy/tj.js',
        // citytravel:'http://'+host+'/springTravel/query',
      },
      visible:false,
      //详情
      modalwantedcircular:{}
    }
  },
  methods:{
    //通缉令接口
    getlist(){
      this.loadingstatus=true
      return this.$axios.get(this.Api.wanted,{}).then(res=>{
        this.wantedcircular.contacts=JSON.parse(res.data.replace("var jsondata=","")).contacts
        this.wantedcircular.data=JSON.parse(res.data.replace("var jsondata=","")).data.slice(0,12)
        // this.wantedcircular=this.wantedcircular.data.slice(0,10)
        // console.log(this.wantedcircular,'格式处理');
        // console.log(res.data,'测试');
        this.loadingstatus=false
      }).catch(err=>{
        console.log(err);
      })
    },
    //modal对话框确定
    clickOk(){
      this.visible = false;
    },
    //点击通缉令卡片
    clickdata(data){
      this.modalwantedcircular=data
      // console.log(this.modalwantedcircular);
      this.visible = true;
    },
  },
  mounted(){
    this.getlist()
  },
}
</script>

<style scoped>
.carddata{
  /* color: rgb(235, 8, 8); */
  height: 150px;
  overflow:hidden;
  text-overflow: ellipsis;
  display:-webkit-box;
  -webkit-box-orient: vertical;
   /* 限制在一个块元素显示的文本的行数。*/
  -webkit-line-clamp:6; 
}
.spandata{
  display:-webkit-box;
  -webkit-box-orient: vertical;
}
.more{
  background-color: #f00;
  width: 10px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px #f00;
}
.card2{
  width: 100%;
  border-radius: 15px;
  box-shadow: 0px 0px 10px #cac8c8;
  margin-top: 20px;
}
.name{
  color:rgb(235, 8, 8);
  font-weight:700;
}
.title{
  color:#f00;
  font-weight:700;
  font-size:1.2em;
  text-shadow: 3px 3px 1px rgba(255, 0, 0, 0.219);
}
.acardhover{
  /* border-radius: 10px; */
}
.acardhover:hover{
  /* box-shadow: 0 0 5px 1px rgb(235, 8, 8); */
  border-radius: 5px;
}
.p{
  font-size: 1.2em;
}
</style>


