<template>
  <a-card title="国内各地区疫情统计汇总" class="card2" :bordered="false">
    <div slot="extra" href="#" class="more"></div>
    <div>
      <a-row>
        <a-col class="header" :lg="{span:3,offset: 3}" :xs="{span:4}" style="border-radius:8px 0 0 8px">
          <p style="margin:10px 0 10px 20px">地区</p>
        </a-col>
        <a-col class="header" :lg="{span:3}" :xs="{span:4}" style="text-align:center;">
          <p style="margin:10px auto">疑似</p>
        </a-col>
        <a-col class="header" :lg="{span:3}" :xs="{span:4}" style="text-align:center;">
          <p style="margin:10px auto">现有</p>
        </a-col>
        <a-col class="header" :lg="{span:3}" :xs="{span:4}" style="text-align:center;">
          <p style="margin:10px auto">累计</p>
        </a-col>
        <a-col class="header" :lg="{span:3}" :xs="{span:4}" style="text-align:center;">
          <p style="margin:10px auto">治愈</p>
        </a-col>
        <a-col class="header" :lg="{span:3}" :xs="{span:4}" style="text-align:center;border-radius:0 8px 8px 0">
          <p style="margin:10px auto">死亡</p>                
        </a-col>
      </a-row>
    </div>

    <!-- 城市具体数值 -->
    <div v-for="(label,index) in Exhibition" :key="index">
      <a-row style="margin-top:10px">
        <a-col :lg="{span:3,offset: 3}" :xs="{span:4}" style="border-radius:8px 0 0 8px;background-color:#00BEC9">
          <p style="margin:10px 0 10px 20px;color:#fff;cursor:pointer;overflow: hidden;text-overflow:ellipsis;white-space:nowrap" @click="clickcityname(index)">
            {{label.provinceName}}<a-icon v-if="label.cityList.length!=0" :type="is_true[index]?'caret-up':'caret-down'" />
          </p>
        </a-col>
        <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
          <p style="margin:10px auto">{{label.suspectedNum}}</p>
        </a-col>
        <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
          <p style="margin:10px auto">{{label.currentConfirmedNum}}</p>
        </a-col>
        <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
          <p style="margin:10px auto">{{label.confirmedNum}}</p>
        </a-col>
        <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
          <p style="margin:10px auto">{{label.curedNum}}</p>
        </a-col>
        <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center;border-radius:0 8px 8px 0">
          <p style="margin:10px auto">{{label.deadNum}}</p>
        </a-col>
      </a-row>
      <template v-for="(lab,index2) in label.cityList">
        <div v-if="is_true[index]" style="margin-top:1px" :key="index2">
          <a-row>
            <a-col :lg="{span:3,offset: 3}" :xs="{span:4}" style="border-radius:8px 0 0 8px;background-color:#5AC6CC">
              <p style="margin:10px 0 10px 20px;color:#fff;overflow: hidden;text-overflow:ellipsis;white-space:nowrap">{{lab.cityName}}</p>
            </a-col>
            <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
              <p style="margin:10px auto">{{lab.suspectedNum}}</p>
            </a-col>
            <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
              <p style="margin:10px auto">--</p>
            </a-col>
            <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
              <p style="margin:10px auto">{{lab.confirmedNum}}</p>
            </a-col>
            <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center">
              <p style="margin:10px auto">{{lab.curedNum}}</p>
            </a-col>
            <a-col class="bodydata" :lg="{span:3}" :xs="{span:4}" style="text-align:center;border-radius:0 8px 8px 0">
              <p style="margin:10px auto">{{lab.deadNum}}</p>
            </a-col>
          </a-row>
        </div>
      </template>
    </div>

    <div :class="is_discharge?'seemoretrue':'seemore'">
      <a-icon v-if="is_discharge" type="up" style="font-size:30px" @click="seemore"/>
      <a-icon v-else type="down" style="font-size:30px" @click="seemore"/>
    </div>
  </a-card>
</template>

<script>
import Vue from 'vue'
export default {
  data(){
    return{
      //展示的城市列表
      Exhibition:[],
      //全部省份列表
      todayDetailList:[],
      //部分省份列表
      parttodayDetailList:[],
      //是否展开城市列表
      is_discharge:false,
      is_true:{},
      //接口地址
      Api:{        
        getlist:'https://route.showapi.com/2217-2?showapi_appid=674964&showapi_sign=40d10f5336274f45bb4eb7a9b11c1d1e',
      },
    }
  },
  methods:{
    //数据统计接口
    epidnucdata(){
      return this.$axios.post(this.Api.getlist,{}).then(res=>{
        this.todayDetailList=res.data.showapi_res_body.todayDetailList
        this.parttodayDetailList=res.data.showapi_res_body.todayDetailList.slice(0,10)
        this.Exhibition=res.data.showapi_res_body.todayDetailList.slice(0,10)
        // console.log(res,'各地区数据');
      }).catch(err=>{
        console.log(err);
      })
    },
    //隐藏/显示所有列表
    seemore(){
      this.is_discharge=!this.is_discharge
      // console.log(this.is_discharge);
      if(this.is_discharge){
        this.Exhibition=this.todayDetailList
      }else{
        this.Exhibition=this.parttodayDetailList
      }
    },
    //隐藏/显示地区数据
    clickcityname(index){
      // this.is_true[index]=!this.is_true[index]
      // console.log(this.is_true);
      Vue.set(this.is_true, index,!this.is_true[index])
    }
  },
  mounted(){
    this.epidnucdata()
  },
}
</script>

<style scoped>
  .seemore{
    position: relative;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow:0px -40px  50px 10px #fff
  }
  .more{
    background-color: #10AEB5;
    box-shadow: 0 0 5px #10AEB5;
    width: 10px;
    height: 30px;
    border-radius: 5px;
  }
  .card2{
    width: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #cac8c8;
    margin-top: 20px;
  }
  .seemoretrue{
    position: relative;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header{
    background-color:#F5F6F7;
    border:1px solid #fff
  }
  .header:hover{
    background-color:rgb(221, 221, 233);
    transition: 0.2s;
  }
  .bodydata{
    background-color:#F5F6F7;
  }
  .bodydata:hover{
    background-color:rgb(221, 221, 233);
    transition: 0.2s;
  }
</style>


