<template>
  <a-card title="新闻资讯" class="card2" :bordered="false">
    <div slot="extra" href="#" class="more"></div>
    <a-tabs default-active-key="1">     
      <a-tab-pane key="1">
        <span slot="tab">
          <a-icon type="weibo-circle" />
          微博热搜榜
        </span>
        <a-spin :spinning="spinning1">
          <p>时间：{{newsranking.time}}</p>
          <a-divider style="margin:13px 0"/>
          <ul style="list-style:none;padding:0px">
            <li class="ulli" v-for="(newsdata,index) in newsranking.data" :key="index">
              <a-tag v-if="newsdata.ranking==0" color="#E54343" class="tags">
                {{newsdata.ranking+1}}
              </a-tag>
              <a-tag v-else-if="newsdata.ranking==1" color="#FE7C1D" class="tags">
                {{newsdata.ranking+1}}
              </a-tag>
              <a-tag v-else-if="newsdata.ranking==2" color="#FBCA45" class="tags">
                {{newsdata.ranking+1}}
              </a-tag>
              <a-tag v-else-if="newsdata.ranking!=undefined" color="#AFB5C5" class="tags">
                {{newsdata.ranking+1}}
              </a-tag>
              <div v-else class="tag2"></div>
              <a :href="newsdata.url" target="_blank" class="rank">
                {{ newsdata.title }}
              </a>
              <a-icon v-if="newsdata.hot" type="fire" theme="filled" style="color:#F4482F"/>
              <a-tag v-else color="cyan">广告</a-tag>
              <a-tooltip v-if="newsdata.hot" :title="`热度值：${newsdata.hot}`">
                <span>{{newsdata.hot}}</span>
              </a-tooltip>
              <a-tag v-if="newsdata.hot>=10000000" color="#9A0802">爆</a-tag>
              <a-tag v-else-if="newsdata.hot>=5000000" color="#E76615">沸</a-tag>
              <a-tag v-else-if="newsdata.hot>=2000000" color="#FF9406">热</a-tag>
              <a-divider style="margin:13px 0"/>
            </li>
          </ul>
        </a-spin>
      </a-tab-pane>
      <!-- <a-tab-pane key="2">
        <span slot="tab">
          <a-icon type="align-left" />
          实时热点
        </span>
        <a-spin :spinning="spinning2">
          <a-timeline class="timeline">
            <a-timeline-item v-for="(newsdata,index) in newslistdata" :key="index">
              <a :href="newsdata.link" class="newslink" target="_blank">{{newsdata.title}}</a>
            </a-timeline-item>
          </a-timeline>
        </a-spin>
      </a-tab-pane> -->
      <a-tab-pane key="3">
        <span slot="tab">
          <a-icon type="history" />
          历史上的今天
        </span>
        <a-spin :spinning="spinning3">
          <div style="margin:10px 0 30px 0">
            <a-tag color="#AFB5C5">
              <span style="font-size:2em">{{historytime.slice(0,2)}}</span>
            </a-tag>
            <span style="margin-right:6px">月</span>
            <a-tag color="#AFB5C5">
              <span style="font-size:2em">{{historytime.slice(3,6)}}</span>
            </a-tag>
            <span>号</span>
          </div>
          <a-timeline style="margin:20px 0 0px 3px">
            <a-timeline-item style="margin-bottom:5px" v-for="(history,index) in historydata" :key="index">
              <a-icon slot="dot" type="clock-circle-o" style="font-size: 15px;color:#FD6926" />
              {{history.date.slice(0,history.date.indexOf("年")+1)}}&nbsp;{{history.title}}
            </a-timeline-item>
          </a-timeline>
        </a-spin>
      </a-tab-pane>
    </a-tabs>
  </a-card>
</template>

<script>
export default {
  data(){
    return{
      //新闻信息
      newslistdata:[],
      //热搜信息
      newsranking:[],
      //历史
      historydata:[],
      //历史时间
      historytime:'',
      spinning1:true,
      spinning2:true,
      spinning3:true,
      //接口地址
      Api:{
        newsdata:'https://v2.alapi.cn/api/tophub/get?token=LwExDtUWhF3rH5ib&type=toutiao',
        newsranking:'https://api.vvhan.com/api/wbhot',
        history:'https://api.oick.cn/lishi/api.php',
      },
    }
  },
  methods:{
    //热搜接口
    newranking(){
      return this.$axios.get(this.Api.newsranking,{}).then(res=>{
        // console.log(res.data.data);
        this.newsranking=res.data
        let num=0
        let data=res.data
        this.spinning1=false
        console.log(res.data,'热搜榜1');
        for(let i=0;i<=data.data.length;i++){
          if(data.data[i].hot!==""){
            data.data[i].ranking=num
            num++
          }
        }
        this.newsranking=data
      }).catch(err=>{
        console.log(err);
      })
    },
    //新闻接口
    newslist(){
      this.loadingstatus=true
      return this.$axios.get(this.Api.newsdata,{}).then(res=>{
        this.newslistdata=res.data.data.list
        // console.log(this.newslistdata,'newsdata');
        this.spinning2=false
      }).catch(err=>{
        console.log(err);
      })
    },
    //历史上的今天接口
    history(){
      return this.$axios.get(this.Api.history,{}).then(res=>{
        this.historytime=res.data.day
        this.historydata=res.data.result.reverse()
        // console.log(this.historydata,'历史上的今天');
        this.spinning3=false
      }).catch(err=>{
        console.log(err);
      })
    }
  },
  mounted(){
    this.newranking()
    this.newslist()
    this.history()
  },
}
</script>

<style scoped>
  .card2{
    width: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #cac8c8;
    margin-top: 20px;
    overflow: auto;
    height: 700px;
  }
  .more{
    background-color: #10AEB5;
    box-shadow: 0 0 5px #10AEB5;
    width: 10px;
    height: 30px;
    border-radius: 5px;
    overflow: hidden;
  }
  .timeline{
    margin-top: 20px;
  }
  .newslink{
    font-size: 1.1em;
    margin-left: 10px;
    margin-bottom: 10px;
    display: inline-block;
    transition: 0.2s;
  }
  .newslink:hover{
    color: orange;
  }
  .rank:hover{
    color: orange;
  }
  .tags{
    border-radius: 10px 0 10px 0;
  }
  .tag2{
    height: 10px;
    width: 10px;
    display: inline-block;
    border-radius: 5px;
    margin: 0 10px;
    border: 1px solid #87E8DE;
    background-color: #87E8DE;
    box-shadow: 0 0 8px #87E8DE;
  }
  /* .ranking{
    display: inline-block;
    border: 1px solid #000;
    height: 20px;
    width: 20px;
    border-radius: 10px;
  } */
  .ulli{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
</style>


