<template>
  <a-card title="数据统计" class="card" :loading="loadingstatus" :bordered="false">
    <div slot="extra" href="#" class="more"></div>
    <a-row :gutter="[0,50]">
      <a-col :sm="{ span: 12 }" :lg="{ span: 4}">
        <a-icon type="bar-chart" style="font-size: 1.2em" />
        <span style="margin-left:5px;font-size: 1.2em">国内疫情</span>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 8, offset: 12 }">                
        <span style="margin-left:5px;font-size: 1em;color:#9195A3">数据更新至：{{updateTime}}</span>
      </a-col>
    </a-row>
    <a-row :gutter="[0,30]">
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 1}">
        <div class="datetable">
          <p style="margin-bottom:0px">现有确诊</p>
          <p style="color:#FF6A57;font-size:2em;margin-bottom:0px">{{todayStatictic.confirmedNum}}</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span style="color:#FF6A57">--</span>                  
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 2}">
        <div class="datetable">
          <p style="margin-bottom:0px">无症状</p>
          <p style="color:#FF6A57;font-size:2em;margin-bottom:0px">{{todayStatictic.asymptomaticNum}}</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span v-if="todayStatictic.asymptomaticIncr>=0" style="color:#FF6A57">+{{todayStatictic.asymptomaticIncr}}</span>
          <span v-else style="color:#FF6A57">-{{todayStatictic.asymptomaticIncr}}</span>
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 2}">
        <div class="datetable">
          <p style="margin-bottom:0px">累计治愈</p>
          <p style="color:#10AEB5;font-size:2em;margin-bottom:0px">{{todayStatictic.curedNum}}</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span v-if="todayStatictic.curedIncr>=0" style="color:#10AEB5">+{{todayStatictic.curedIncr}}</span>
          <span v-else style="color:#10AEB5">-{{todayStatictic.curedIncr}}</span>
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 2}">
        <div class="datetable">
          <p style="margin-bottom:0px">累计死亡</p>
          <p style="color:#4D5054;font-size:2em;margin-bottom:0px">{{todayStatictic.deadNum}}</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span v-if="todayStatictic.deadIncr>=0" style="color:#4D5054">+{{todayStatictic.deadIncr}}</span>
          <span v-else style="color:#4D5054">-{{todayStatictic.deadIncr}}</span>
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 1}">
        <div class="datetable">
          <p style="margin-bottom:0px">境外输入</p>
          <p style="color:#476DA0;font-size:2em;margin-bottom:0px">{{todayStatictic.externalConfirmedNum}}</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span v-if="todayStatictic.externalConfirmedIncr>=0" style="color:#476DA0">+{{todayStatictic.externalConfirmedIncr}}</span>
          <span v-else style="color:#476DA0">-{{todayStatictic.externalConfirmedIncr}}</span>
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 2}">
        <div class="datetable">
          <p style="margin-bottom:0px">现有疑似</p>
          <p style="color:#EC9217;font-size:2em;margin-bottom:0px">--</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span style="color:#EC9217">--</span>                  
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 2}">
        <div class="datetable">
          <p style="margin-bottom:0px">现有重症</p>
          <p style="color:#545499;font-size:2em;margin-bottom:0px">--</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span style="color:#545499">--</span>                  
        </div>
      </a-col>
      <a-col :sm="{ span: 12 }" :lg="{ span: 4, offset: 2}">
        <div class="datetable">
          <p style="margin-bottom:0px">累计确诊</p>
          <p style="color:#E83132;font-size:2em;margin-bottom:0px">--</p>
          <span style="color:#9195A3">较昨日</span>&nbsp;
          <span v-if="todayStatictic.confirmedIncr>=0" style="color:#E83132">+{{todayStatictic.confirmedIncr}}</span>
          <span v-else style="color:#E83132">-{{todayStatictic.confirmedIncr}}</span>
        </div>
      </a-col>
    </a-row>
  </a-card>
</template>

<script>
export default {
  data(){
    return{
      //接口地址
      Api:{        
        getlist:'https://route.showapi.com/2217-2?showapi_appid=674964&showapi_sign=40d10f5336274f45bb4eb7a9b11c1d1e',
      },
      loadingstatus:true,
      //总数据
      bigdata:[],
      //更新时间
      updateTime:undefined,
      //详细数据
      todayStatictic:[],
    }
  },
  methods:{
    //数据统计接口
    epidnucdata(){
      this.loadingstatus=true
      return this.$axios.post(this.Api.getlist,{}).then(res=>{
        this.bigdata=res.data
        this.updateTime=res.data.showapi_res_body.updateTime
        this.todayStatictic=res.data.showapi_res_body.todayStatictic 
        this.loadingstatus=false
        // console.log(res,'数据统计');
        // for(let i=0;i<=this.todayDetailList.length;i++){
        //   this.highDangerNum+=this.todayDetailList[i].highDangerNum
        //   this.midDangerNum+=this.todayDetailList[i].midDangerNum
        //   console.log(highDangerNum,midDangerNum);
        // }
        // console.log(this.bigdata);
      }).catch(err=>{
        console.log(err);
      })
    },
  },
  mounted(){
    this.epidnucdata()
  },
}
</script>

<style scoped>
  .datetable{
    width:100%;
    height:100%;
    border-radius: 100px;
    padding-top: 10px;
    padding-bottom: 10px;
    transition: 0.2s;
  }
  .datetable:hover{
    background-color:#F5F5F6;
    border-radius: 30px;
  }
  .card{
    width: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #cac8c8;
    margin-top: 20px;
    padding-bottom: 20px;
    text-align: center;
  }
</style>


