<template>
  <a-card title="天气预报" class="card2" :bordered="false">
    <div slot="extra" href="#" class="more"></div>
    <a-spin :spinning="spinning">
      <div class="nowtemperature">
        <a-row>
          <a-col style="float:left">
            <h2 style="color:rgb(77, 80, 84)">当前气温：{{cityweather.now}}℃</h2>
          </a-col>
          <a-col style="float:right">
            <h2 style="color:rgb(77, 80, 84)">
              <a-icon type="global" style="color:#5383FF"/>地理位置：{{cityweather.name}}
            </h2>
          </a-col>        
        </a-row>
        <a-row :gutter=[0,10]>
          <a-col :sm="{ span: 24 }" :lg="{ span: 10,offset:1}">
            <a-card>
              <a-statistic
                :title="`昨日最${cityweather.yesterday.high}`"
                :value="`${cityweather.highdisparity}`"
                :precision="0"
                suffix="℃"
                :value-style="cityweather.high_istop>0?{ color: '#cf1322' }:{ color: '#5470C6' }"
                style="margin-right: 50px"
              >
                <template #prefix>
                  今日<a-icon :type="cityweather.high_istop>0?'arrow-up':'arrow-down'" />
                </template>
              </a-statistic>
            </a-card>
          </a-col>
          <a-col :sm="{ span: 24 }" :lg="{ span: 10,offset:2}">
            <a-card>
              <a-statistic
                :title="`昨日最${cityweather.yesterday.low}`"
                :value="`${cityweather.lowdisparity}`"
                :precision="0"
                suffix="℃"
                class="demo-class"
                :value-style="cityweather.low_istop>0?{ color: '#cf1322' }:{ color: '#5470C6' }"
              >
                <template #prefix>
                  今日<a-icon :type="cityweather.low_istop>0?'arrow-up':'arrow-down'" />
                </template>
              </a-statistic>
            </a-card>
          </a-col>
        </a-row>
        <div class="reminddiv">
          <div class="remind">
            <a-icon type="sound" style="font-size:20px"/>
            <span>
              {{cityweather.remind}}
            </span>
          </div>
        </div>
      </div>
    </a-spin>
    <div ref="weatherdiv" style="width:100%;height:400px"></div>
  </a-card>
</template>

<script>
import * as echarts from 'echarts';

export default {
  data(){
    return{
      //接口地址
      Api:{        
        weather:'https://api.vvhan.com/api/weather?type=week',
      },
      //城市天气预报信息
      cityweather:{
        name:'',
        date:[],
        high:[],
        low:[],
        yesterday:[],
        now:0,
        high_istop:true,
        low_istop:true,
        highdisparity:0,
        lowdisparity:0,
        remind:'',
      },
      spinning:true,
    }
  },
  methods:{
    //天气预报
    weather(){
      return this.$axios.get(this.Api.weather,{}).then(res=>{        
        this.cityweather.name=res.data.data.city
        this.cityweather.now=res.data.data.wendu
        this.cityweather.yesterday=res.data.data.yesterday
        this.cityweather.remind=res.data.data.ganmao
        for(let i=0;i<res.data.data.forecast.length;i++){
          this.cityweather.date[i]=res.data.data.forecast[i].date
          this.cityweather.high[i]=res.data.data.forecast[i].high
          this.cityweather.low[i]=res.data.data.forecast[i].low
        }
        for(let i=0;i<res.data.data.forecast.length;i++){
          this.cityweather.high[i]=this.cityweather.high[i].replace("高温 ","")
          this.cityweather.high[i]=this.cityweather.high[i].replace("℃","")
          this.cityweather.low[i]=this.cityweather.low[i].replace("低温 ","")
          this.cityweather.low[i]=this.cityweather.low[i].replace("℃","")
        }
        let gaowen=this.cityweather.yesterday.high.replace("高温 ","")
        gaowen=gaowen.replace("℃","")
        let diwen=this.cityweather.yesterday.low.replace("低温 ","")
        diwen=diwen.replace("℃","")
        if(parseInt(this.cityweather.high[0])>=parseInt(gaowen)){
          this.cityweather.highdisparity=parseInt(this.cityweather.high[0])-parseInt(gaowen)
          this.cityweather.high_istop=true
        }else{
          this.cityweather.highdisparity=parseInt(gaowen)-parseInt(this.cityweather.high[0])
          this.cityweather.high_istop=false
        }        
        if(parseInt(this.cityweather.low[0])>=parseInt(diwen)){
          this.cityweather.lowdisparity=parseInt(this.cityweather.low[0])-parseInt(diwen)
          this.cityweather.low_istop=true
        }else{
          this.cityweather.lowdisparity=parseInt(diwen)-parseInt(this.cityweather.low[0])
          this.cityweather.low_istop=false
        }
        let myChart = echarts.init(this.$refs.weatherdiv)
        myChart.setOption({
          title: {
            text: '近'+res.data.data.forecast.length+'日天气'
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {},
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.cityweather.date
          },
          yAxis: {
            type: 'value',
            axisLabel: {
              formatter: '{value} °C'
            }
          },
          color:['#cf1322', '#5470C6'],
          series: [
            {
              name: '最高温度',
              type: 'line',
              data: this.cityweather.high,
              smooth: true,
              // markPoint: {
              //   data: [
              //     { type: 'max', name: 'Max' },
              //     { type: 'min', name: 'Min' }
              //   ]
              // },
              markLine: {
                data: [{ type: 'average', name: 'Avg' }]
              }
            },
            {
              name: '最低温度',
              type: 'line',
              data: this.cityweather.low,
              smooth: true,
              // markPoint: {
              //   data: [{ name: '周最低', value: -2, xAxis: 1, yAxis: -1.5 }]
              // },
              markLine: {
                data: [
                  { type: 'average', name: 'Avg' },
                  // [
                  //   {
                  //     symbol: 'none',
                  //     x: '90%',
                  //     yAxis: 'max'
                  //   },
                  //   {
                  //     symbol: 'circle',
                  //     label: {
                  //       position: 'start',
                  //       formatter: 'Max'
                  //     },
                  //     type: 'max',
                  //     name: '最高点'
                  //   }
                  // ]
                ]
              }
            }
          ]
        })            
        this.spinning=false
      }).catch(err=>{
        console.log(err);
      })
    },
  },
  mounted(){
    this.weather()
  },
}
</script>

<style scoped>
  .reminddiv{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
  }
  .remind{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    background-color: #ebebeb;
    border-radius: 20px;
    padding: 0px 10px;
  }
  .card2{
    width: 100%;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #cac8c8;
    margin-top: 20px;
  }
  .more{
    background-color: #10AEB5;
    box-shadow: 0 0 5px #10AEB5;
    width: 10px;
    height: 30px;
    border-radius: 5px;
  }
  .nowtemperature{
    width:100%;
    margin-bottom: 30px;
  }
</style>


